import { useState, useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated, useTransition } from "react-spring";
import Grid from "@material-ui/core/Grid";

import csgoBg from "../../Images/cslp.png";
import valorantBg from "../../Images/valorantlp.png";
import freefireBg from "../../Images/freefirecard.png";
import codBg from "../../Images/cofdmlp.png";
import pubgBg from "../../Images/pubglp.png";

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    backgroundColor: "#09090A",
    width: "100vw",
    minHeight: "100vh",
    minHeight: "1000px",
  },
  textHead: {
    color: "#E72727",
    fontSize: "20px",
    fontWeight: "800",
    letterSpacing: "2px",
    borderBottom: "2px solid #E72727",
    padding: "10px",
    width: "100%",
  },
  textNormal: {
    color: "white",
    fontSize: "16px",
    fontWeight: "400",
    letterSpacing: "1px",
    padding: "10px",
    width: "100%",
  },
  textDiv: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    width: "70%",
  },
  normalHead: {
    color: "white",
    fontSize: "18px",
    fontWeight: "900",
    lineHeight: "25px",
    letterSpacing: "4px",
    padding: "10px",
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      fontSize: "30px",
      lineHeight: "40px",
    },
  },
  mainGrid: {
    position: "absolute",
    minHeight: "100vh",
    width: "80vw",
    padding: "200px 0px",
  },
  img: {},
  gameBg: {
    height: "400px",
    width: "300px",
    objectFit: "contain",
    cursor: "pointer",
    boxShadow: "3px 3px 6px black",
    borderRadius: "10px",
  },
  icon: {
    height: "20px",
    width: "20px",
    alignSelf: "center",
    padding: "20px",
  },
  poster: {
    width: "300px",
  },
}));

const Games = (props) => {
  const classes = useStyles();

  const styles1 = useSpring({
    marginRight: 0,
    opacity: 1,
    display: "flex",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    from: { marginRight: 100, opacity: 0 },
    config: { duration: 600 },
  });

  const games = [
    ({ style }) => (
      <animated.div style={{ ...style }}>
        {" "}
        <img
          alt="csgo image"
          style={{ ...style }}
          className={classes.gameBg}
          src={csgoBg}
        />{" "}
      </animated.div>
    ),
    ({ style }) => (
      <animated.div style={{ ...style }}>
        {" "}
        <img
          alt="valorant image"
          tyle={{ ...style }}
          className={classes.gameBg}
          src={valorantBg}
        />{" "}
      </animated.div>
    ),
    ({ style }) => (
      <animated.div style={{ ...style }}>
        {" "}
        <img
          alt="COD mobile image"
          style={{ ...style }}
          className={classes.gameBg}
          src={codBg}
        />{" "}
      </animated.div>
    ),
    ({ style }) => (
      <animated.div style={{ ...style }}>
        {" "}
        <img
          alt="free fire image"
          style={{ ...style }}
          className={classes.gameBg}
          src={freefireBg}
        />{" "}
      </animated.div>
    ),
    ({ style }) => (
      <animated.div style={{ ...style }}>
        {" "}
        <img
          alt="free fire image"
          style={{ ...style }}
          className={classes.gameBg}
          src={pubgBg}
        />{" "}
      </animated.div>
    ),
  ];

  const [index, setIndex] = useState(0);

  const onClick = useCallback(
    () => setIndex((index) => (index + 1) % games.length),
    []
  );

  const transition = useTransition(index, (p) => p, {
    from: {
      opacity: 0,
      transform: "translate3d(10%,0,0)",
      position: "absolute",
    },
    enter: { opacity: 1, transform: "translate3d(0%,0,0)", position: "static" },
    leave: {
      opacity: 0,
      transform: "translate3d(-10%,0,0)",
      position: "absolute",
    },
  });

  return (
    <>
      <div className={classes.mainDiv} />
      <Grid
        container
        className={classes.mainGrid}
        justify="center"
        alignItems="center"
        spacing={3}
      >
        <Grid item xs={12} md={5} container justify="center">
          <animated.div style={styles1}>
            <div className={classes.textDiv}>
              <span className={classes.textHead}>SUPPORTED GAMES</span>
              <span className={classes.textNormal}>
                We support a variety of games over different platforms. If you
                don't find your favorite game please let us know and we will be
                more than happy to add your favorite game. We are always open
                for suggestions from you people on our social handles and try
                our best to keep up.
              </span>
            </div>
          </animated.div>
        </Grid>
        <Grid
          item
          xs={12}
          md={5}
          className={classes.poster}
          container
          justify="center"
        >
          <div onClick={onClick}>
            {transition.map(({ item, props, key }) => {
              const Game = games[item];
              return <Game key={key} style={props} />;
            })}
          </div>
          <img
            alt="forward icon"
            className={classes.icon}
            src="https://img.icons8.com/metro/26/ffffff/forward.png"
          />
        </Grid>
      </Grid>
    </>
  );
};

export default Games;
